.article-list{
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.highlight-article{
  display: inline-block;
  width: 48%;
}

.secondary-article{
  width: 23%;
}

.minimal-article{
  width: 31%;
}

/*DIVIDER*/
.divider{
  width: 100%;
  border: 1px solid #E4E4E4;
  margin-top: 26px;
}

/*MOBILE*/
@media (max-width: 1170px){
  .divider{
    display: none;
  }

  .article{
    margin-bottom: 50px;
  }

  /*ARTICLES HIGHLIGHT*/
  .highlight-article{
    display: inline-block;
    width: 100%;
  }

  /*ARTICLES SECONDARY*/
  .secondary-article{
    width: 48%;
  }

  /*ARTICLES MINIMAL*/
  .minimal-article{
    width: 48%;
  }

}

/*MOBILE SMALL*/
@media (max-width: 750px){

  /*ARTICLES HIGHLIGHT*/
  .highlight-article{
    display: inline-block;
    width: 100%;
  }

  /*ARTICLES SECONDARY*/
  .secondary-article{
    width: 100%;
  }

  /*ARTICLES MINIMAL*/
  .minimal-article{
    width: 100%;
  }
}


